import logo from "../../assets/img/chepe.png";

const Index = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-2">
          {" "}
          <img
            src={logo}
            className="img-fluid"
            alt="logo"
            style={{ width: 200, height: 90 }}
          />
        </div>
        <div className="col-sm-10">
          <h3>
            <strong>¡Chepe te lleva!</strong> (Red de Conductores de Motos)
          </h3>
          <p>
            ¡Chepe te lleva! es una plataforma tecnológica que sirve de
            intermediación entre clientes que necesitan trasladarse ya sea en
            moto, carro sencillo o carros VIP con prestadores de servicios que
            pueden prestarle el servicio.
          </p>
          <div />
        </div>
        <div className="row">
          <h3 className="text-center text-muted">
            <strong>Características</strong>
          </h3>
          <br />

          <h5>
            <strong>Amplia portafolio de prestadores:</strong>
          </h5>
          <p>
            Los proveedores de servicios tienen diferentes marcas y modelos de
            vehículos para facilitarle el traslado de forma cómoda y segura.
            <br />
            <strong>Seguridad:</strong> &nbsp; Cada proveedor de serivicio pasa
            por una regurosa investigación para determinar la aceptación dentro
            del portal, garantizando así el suministro de personal calificado y
            seguro. <br />
            <strong>Calificaciones:</strong> &nbsp; tanto el cliente como el
            proveedor de servicios deben realizar una calificación. El cliente
            calificará al prestador y vicerversa. De esta se lleva un record en
            la calidad de prestación y como cliente.
          </p>
        </div>
      </div>
    </div>
  );
};
export default Index;
