import Timeline from "@mui/lab/Timeline";
import TimelineItem from "@mui/lab/TimelineItem";
import TimelineSeparator from "@mui/lab/TimelineSeparator";
import TimelineConnector from "@mui/lab/TimelineConnector";
import TimelineContent from "@mui/lab/TimelineContent";
import TimelineDot from "@mui/lab/TimelineDot";
import TimelineOppositeContent from "@mui/lab/TimelineOppositeContent";
import dos from "../assets/img/dos.webp";
import toga from "../assets/img/toga.jpg";
import dbase from "../assets/img/dbase.jpg";
import visualfox from "../assets/img/visualfox.jpeg";
import java from "../assets/img/java.png";
import react from "../assets/img/react.jpeg";

const Experiencia = () => {
  return (
    <div className="container">
      <div className="row mt-3 mb-3">
        <h1 className="text-center">Experiencia</h1>
      </div>
      <div className="row">
        <Timeline position="alternate">
          <TimelineItem>
            <TimelineOppositeContent color="text.secondary">
              <strong>1992</strong>
              <br />
              <img
                src={dos}
                alt={"img"}
                loading="img"
                style={{ width: 150, height: 150, borderRadius: 100 }}
              />{" "}
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              Desarrollo mi primer software para el área de Contabilidad en la
              empresa donde laboraba.
              <br />
              Desarrollo los módulos de nómina, conciliación bancaria y cartera
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent color="text.secondary">
              <strong>1993</strong>

              <br />
              <img
                src={toga}
                alt={"img"}
                loading="img"
                style={{ width: 150, height: 150, borderRadius: 100 }}
              />
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              {" "}
              Termino mi carrera de Desarrollador de Software.
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent color="text.secondary">
              <strong>1993 a 1995</strong>

              <br />
              <img
                src={dbase}
                alt={"img"}
                loading="img"
                style={{ width: 150, height: 150, borderRadius: 100 }}
              />
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              Desarrollo de software bajo el sistema operativo DOS de Microsoft
              con lenguajes de programación como c, qbasic, turbo pascal, dbase,
              dbase III plus y fox.
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent color="text.secondary">
              <strong>1995 a 2000</strong>

              <br />
              <img
                src={visualfox}
                alt={"img"}
                loading="img"
                style={{ width: 150, height: 150, borderRadius: 100 }}
              />
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              Desarrollo de software en entornos visuales con windows y
              lenguajes como Visual Fox Pro, Visual Basic con DB SQl y MySQL.
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent color="text.secondary">
              <strong>2000 a 2015</strong>

              <br />
              <img
                src={java}
                alt={"img"}
                loading="img"
                style={{ width: 150, height: 150, borderRadius: 100 }}
              />
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              Desarrollo de software con Java standar para windows y Linux.
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent color="text.secondary">
              <strong>2015 a 2025</strong>

              <br />
              <img
                src={react}
                alt={"img"}
                loading="img"
                style={{ width: 150, height: 150, borderRadius: 100 }}
              />
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              Desarrollo de software en entornos modernos en javascript con
              framework como React, React Native, Angular, Node JS, Python
            </TimelineContent>
          </TimelineItem>
        </Timeline>
      </div>
    </div>
  );
};
export default Experiencia;
