import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import CardActionArea from "@mui/material/CardActionArea";
import soft1 from "../assets/img/soft1.jpg";
import plomero from "../assets/img/plomero.webp";
import alojamento from "../assets/img/alojamiento1.webp";
import taxysmoto from "../assets/img/taxysmoto.png";
import chepe from "../assets/img/chepe.png";
import erp from "../assets/img/erp.png";
import ssp from "../assets/img/ssp.png";
import ModalView from "../paginas/ModalView.jsx";
import { useState } from "react";
import CardActions from "@mui/material/CardActions";
import Button from "@mui/material/Button";

const Portafolio = () => {
  const [mostrarModal, setMostrarModal] = useState(false);
  const [llamador, setLlamador] = useState("");

  const abrirModal = (e, nbre) => {
    e.preventDefault();
    setLlamador(nbre);
    setMostrarModal(true);
  };

  const cerrarModal = () => {
    setMostrarModal(false);
  };

  return (
    <div className="container">
      <div className="row mt-3 mb-3">
        <h1 className="text-center">Portafolio</h1>
      </div>
      <div className="row">
        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={erp} alt="ERP" />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  ERP Soft
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Software de manejo administrativo.
                  <br />
                  Módulos de Compras, Facturación, Caja, Bancos, Inventario,
                  Cartera, Nómina, Activos, Mantenimiento, Presupuesto y
                  Contabilidad.
                  <br />
                  <br />
                  <br />
                  <br />
                  <br />
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "erp")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>

        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={ssp} alt="SSP" />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  SSP Soft
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Software de Seguimiento a Proyectos para
                  <br />
                  Fundaciones y ONG's que ejecutan proyectos.
                  <br />
                  Registre sus proyectos, asigne actividades y tareas a los
                  equipos de trabajo y a los beneficiarios del proyecto, haga
                  seguimiento y audite directamente en tiempo real el avance de
                  éste.
                  <br />
                  <br />
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "ssp")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>

        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={plomero}
                alt="redework"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Redework
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Red de prestadores de servicios en casa.
                  <br />
                  App que permite contratar cualquier tipo de oficio para una
                  fecha y hora requerida por el cliente.
                  <br />
                  Servicios como plomeria, pintura, arreglo de
                  electrodomesticos, etc hasta servicios profesionales como
                  desarrolladores de software, diseñadores gráficos, médicos,
                  enefermeras, abogados, contadores, en fin...
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "redework")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>

        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={alojamento}
                alt="qartos"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  qartos
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Red de arrendamiento de viviendas y habitaciones.
                  <br />
                  Es una App que permite contratar cualquier arrendimientos de
                  cualquier tipo. Puede ser una habitación, una casa o
                  apartamento, una finca o cualquier tipo de propiedad.
                  <br />
                  El arriendo se realiza en cualquier periodo de tiempo, es
                  decir, por días, meses o años.
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "qartos")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>
      </div>

      <div className="row mt-3">
        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={taxysmoto}
                alt="taxysmoto"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Taxys Moto!
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Red prestadora de servicios de transporte en moto.
                  <br />
                  App de intermediación entre clientes que necesitan
                  transportarse y proveedores de servicios con moto que dan
                  solución a la necesidad del cliente.
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "taxysmoto")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>

        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={chepe} alt="ERP" />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Chepe te lleva!
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Servicio de transporte en vehiculos privados.
                  <br />
                  App de intermediación entre clientes que requieren
                  transportarse ya sea en moto, carros o carros VIP, con
                  prestadores de servicios que solucionan la necesidad de
                  transporte delcliente.
                  <br />
                  <br />
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "chepe")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>

        <div className="col-sm-3">
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={soft1} alt="ERP" />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Auditor del SICC
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Software auditor de disponibilidad del Sistema de Control y
                  Contabilización (SICC) de Concesionarias Viales
                  <br />
                  El software audita la estabilidad del software SICC de la
                  Concesionaria Vial.
                  <br />
                  <br />
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" onClick={(e) => abrirModal(e, "auditor")}>
                Ver Detalle
              </Button>
            </CardActions>
          </Card>
        </div>
      </div>
      {mostrarModal && (
        <ModalView llamador={llamador} cerrarModal={cerrarModal} />
      )}
    </div>
  );
};

export default Portafolio;
