import logo from "../../assets/img/redework.png";

const Index = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-2">
          {" "}
          <img
            src={logo}
            className="img-fluid"
            alt="logo"
            style={{ width: 200, height: 90 }}
          />
        </div>
        <div className="col-sm-10">
          <h3>
            <strong>Redework</strong> (Red de Trabajadores Independientes)
          </h3>
          <p>
            Redework es una plataforma tecnológica que sirve de intermediación
            entre clientes que necesitan realizar trabajos en sus casas u
            oficinas en determinadas fechas y horario con prestadores de
            servicios que pueden realizar el trabajo.
            <br />
            La prestación de servicios no se limita solo a oficios no
            calificados, sino también a profesionales que prestan servicios de
            forma independiente.
          </p>
          <div />
        </div>
        <div className="row">
          <h3 className="text-center text-muted">
            <strong>Características</strong>
          </h3>
          <br />

          <h5>
            <strong>Amplia gama de oficios y profesionales:</strong>
          </h5>
          <p>
            Los proveedores de servicios pueden ser profesionales o personas que
            prestan servicios específicos que no requieren estudios formales.
            Eso permite tener un amplio portafolio de prestadores y servicios
            para suministrar a los clientes.
            <br />
            <strong>Trabajos en fecha y horarios puntuales:</strong> &nbsp; La
            mayoría de personas tienen trabajos en horarios diferentes y
            requieren que se les presten servicios en fecha y horarios
            puntuales, debido a que en esa fecha y hora es que se encuentran en
            sus casas, para esos casos existen proveedores de servicios
            dispuestos para las condiciones de tiempo del cliente.
            <br />
            <strong>Seguridad:</strong> &nbsp; Cada proveedor de serivicio pasa
            por una regurosa investigación para determinar la aceptación dentro
            del portal, garantizando así el suministro de personal calificado y
            seguro. <br />
            <strong>Calificaciones:</strong> &nbsp; tanto el cliente como el
            proveedor de servicios deben realizar una calificación. El cliente
            calificará al prestador y vicerversa. De esta se lleva un record en
            la calidad de prestación y como cliente.
          </p>
        </div>
      </div>
    </div>
  );
};
export default Index;
