import jdpc from "../assets/img/jdpc.png";

const AcercaDe = () => {
  return (
    <div className="container-fluid">
      <div className="row mt-3 mb-3">
        <div className="col-sm-5 text-center m-3">
          <h1>Código limpio</h1>
          <hr />
          <p>
            Soy desarrollador de software desde hace poco más de 32 años.
            <br />
            <br /> Desde mi inicio en este fascinante mundo del código de
            programación, me interesó el aprendisaje integral de todo, desde las
            pequeñas y simples pantallas, hasta el almacenamento y recuperación
            de la información en archivos tipo .bat.
            <br />
            <br />
            Con los nuevos modelos, donde se separa el código y las funciones
            del software, a través del front-end y del back-end, con entornos de
            Modelo Vista Controlador (MVC), se hace más fácil la implementación,
            escalable y de fácil mantenimiento.
            <br />
            <br />
            Me gustan los proyectos donde actúo en ambos frentes, tanto del
            front como del back, lo que me permite tener un conocimiento mucho
            más profundo del funcionamiento y codificación del software que se
            ejecuta en ambas partes.{" "}
          </p>
        </div>

        <div className="col-sm-5 text-center m-3">
          <h1>
            <small className="text-body-secondary">
              José David Padilla Cárdenas
            </small>
          </h1>
          <img
            src={jdpc}
            className="rounded mx-auto d-block rounded-circle"
            alt="..."
            style={{ width: 300, height: 300, borderRadius: 10 }}
          />
          <h5 className="text-body-secondary mt-3 mb-3">
            Desarrollador de Software
            <br />
            <small className="text-body-secondary">Full Stack</small>
          </h5>
          <p> 32 años de experiencia</p>
        </div>
      </div>
    </div>
  );
};

export default AcercaDe;
