import logo from "../../assets/img/soft1.jpg";

const Index = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-2">
          {" "}
          <img
            src={logo}
            className="img-fluid"
            alt="logo"
            style={{ width: 200, height: 90 }}
          />
        </div>
        <div className="col-sm-10">
          <h3>
            <strong>Auditor SICC</strong> (Sistema de Control y Contabilización)
          </h3>
          <p>
            Este software esta diseñado para permanecer en contacto constante
            con un servidor, identificando los tiempos de disponibilidad e
            indisponibilidad de cierto software en un servidor.
            <br />
            El diseño original esta orientado para hacerle auditoria al software
            de Control y Contabilización de Concesionaria viales en Colombia,
            sin embargo, puede aplicarse para cualquier tipo de empresa.
          </p>
          <div />
        </div>
        <div className="row">
          <h3 className="text-center text-muted">
            <strong>Características</strong>
          </h3>
        </div>
      </div>
    </div>
  );
};
export default Index;
