import { useState } from "react";
import { Button, Modal } from "react-bootstrap";
import Logo from "../assets/logo.png";
import PromocionSSP from "./ssp/Promocion.jsx";
import Ssp from "./ssp/Index.jsx";
import Erp from "../paginas/erp/Index.jsx";
import Redework from "../paginas/redework/Index.jsx";
import Qartos from "../paginas/qartos/Index.jsx";
import TaxysMoto from "../paginas/taxysmoto/Index.jsx";
import Chepe from "../paginas/chepe/Index.jsx";
import Auditor from "../paginas/auditorSicc/Index.jsx";

function ModalView(props) {
  const [show, setShow] = useState(true);

  const handleClose = () => {
    setShow(false);
    if (props.cerrarModal !== undefined) props.cerrarModal();
  };

  const mostrarPanel = () => {
    if (props.llamador === "promoSSP") {
      return <PromocionSSP />;
    } else if (props.llamador === "erp") {
      return <Erp />;
    } else if (props.llamador === "ssp") {
      return <Ssp />;
    } else if (props.llamador === "redework") {
      return <Redework />;
    } else if (props.llamador === "qartos") {
      return <Qartos />;
    } else if (props.llamador === "taxysmoto") {
      return <TaxysMoto />;
    } else if (props.llamador === "chepe") {
      return <Chepe />;
    } else if (props.llamador === "auditor") {
      return <Auditor />;
    } else {
      return (
        <div className="container">
          <div className="row">
            <h1>Upss!</h1>
            <h2>Esta información aún no está disponible!</h2>
          </div>
        </div>
      );
    }
  };

  const mostrarTitulo = () => {
    if (props.llamador === "promoSSP") {
      return `&nbsp; Descuento del 85% - De&nbsp; <s> $ 20 millones </s>&nbsp; a solo
        $ 3 millones!`;
    }
  };

  return (
    <Modal
      show={show}
      onHide={handleClose}
      backdrop="static"
      keyboard={false}
      size="xl"
    >
      <Modal.Header closeButton>
        <img
          src={Logo}
          className="img-fluid"
          alt="logo"
          style={{ width: 40, height: 30 }}
        />
        {mostrarTitulo()}
      </Modal.Header>
      <Modal.Body>{mostrarPanel()}</Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cerrar
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalView;
