import ssp from "../../assets/img/ssp.png";

const Index = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-2">
          {" "}
          <img
            src={ssp}
            className="img-fluid"
            alt="logo"
            style={{ width: 120, height: 90 }}
          />
        </div>
        <div className="col-sm-10">
          <h3>
            <strong>SSP</strong> (Software de Seguimiento a Proyectos)
          </h3>
          <p>
            SSP es un ecosistema tecnológico para formación, control y medición
            de las asesorías para la gestión avanzada y desarrollo del proyecto
            con canales de atención de fundaciones y ONG's.
          </p>
          <div />
        </div>
        <div className="row">
          <h3 className="text-center text-muted">
            <strong>Características del SSP</strong>
          </h3>
          <br />

          <h5>
            <strong>Formación virtual:</strong>
          </h5>
          <p>
            para la interacción en los procesos de formación y acompañamiento a
            los beneficiarios por parte del equipo de trabajo
            <br />
            <strong>Asignación de tareas:</strong> &nbsp; Permite la asignación
            de tareas según las especificaciones téncicas del proyecto.
            <br />
            <strong>Cronograma de actividades:</strong> &nbsp; Cada actividad
            asignada tanto al equipo de trabajo como a los beneficiarios tiene
            un lapso de tiempo específico de cumplimiento, el cual sirve para
            realizar seguimiento de cumplimiento de éstas según las fechas
            suministradas <br />
            <strong>Monitoreo:</strong> &nbsp; Lleva un seguimiento a las
            actividades asignadas, generando un control y seguimiento a cada una
            de estas y el porcentaje de avance por funcionarios del equipo de
            trabajo, por beneficiario y del proyecto en general.
            <br />
            <strong>Foro:</strong> &nbsp; Permite crear foros de dicusión sobre
            temas específicos relevantes acerca del proyecto o de ineterés que
            identifiquen los directores del proyecto.
            <br />
            <strong> Chat:</strong> &nbsp; Posee un Chat que permite la
            interacción directa entre beneficiarios y equipo de trabajo,
            permitiendo dar soluciones y despejar dudas en tiempo real, así como
            material de consulta.
          </p>
        </div>
      </div>
    </div>
  );
};
export default Index;
