import soft2 from "../assets/img/soft2.jpg";
import porcentaje from "../assets/img/porcentaje.webp";
import ModalView from "./ModalView";
import { useState } from "react";

const Slider = () => {
  const [mostrarModal, setMostrarModal] = useState(false);

  const abrirModal = (e) => {
    e.preventDefault();
    setMostrarModal(true);
  };

  const cerrarModal = () => {
    setMostrarModal(false);
  };

  return (
    <div className="container-fluid">
      <div className="row">
        <div className=" position-relative mb-3">
          <h1 className="position-absolute top-0 start-3 p-3">
            José David Padilla Cárdenas
            <br />
            <small className="text-body-secondary">
              Desarrollador de Software Full Stack
            </small>
          </h1>

          <div className="position-absolute bottom-0 start-1 text-center">
            <a href="#" onClick={(e) => abrirModal(e)} className="cta-button">
              <h1>
                {" "}
                <img
                  src={porcentaje}
                  alt="..."
                  style={{ height: 50, width: 50, borderRadius: 50 }}
                />
                ¡85% de Descuento!{" "}
                <img
                  src={porcentaje}
                  alt="..."
                  style={{ height: 50, width: 50, borderRadius: 50 }}
                />
              </h1>
              <h4>Software de Seguimiento a Proyectos</h4> Ver Descuento
            </a>
          </div>
          <img
            src={soft2}
            className="d-block w-100"
            alt="..."
            style={{ maxHeight: 500, height: "auto" }}
          />
        </div>
      </div>
      {mostrarModal && (
        <ModalView llamador={"promoSSP"} cerrarModal={cerrarModal} />
      )}
    </div>
  );
};

export default Slider;
