import { useState } from "react";
import Box from "@mui/material/Box";
import BottomNavigation from "@mui/material/BottomNavigation";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import AssignmentIndIcon from "@mui/icons-material/AssignmentInd";
import SchoolIcon from "@mui/icons-material/School";
import QrCode2Icon from "@mui/icons-material/QrCode2";
import BookmarksIcon from "@mui/icons-material/Bookmarks";
import AlternateEmailIcon from "@mui/icons-material/AlternateEmail";

const PiePagina = (props) => {
  const [value, setValue] = useState(0);

  const desplazar = (newValue) => {
    setValue(newValue);

    // Mapeo de valores a referencias
    const refMap = {
      0: props.refs.acercaDe,
      1: props.refs.habilidades,
      2: props.refs.experiencia,
      3: props.refs.portafolio,
      4: props.refs.contacto,
    };

    const targetRef = refMap[newValue];
    if (targetRef && targetRef.current) {
      targetRef.current.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  };

  return (
    <div className="container mt-3 mb-3">
      <Box /* sx={{ width: 500 }} */>
        <BottomNavigation
          showLabels
          value={value}
          onChange={(event, newValue) => desplazar(newValue)}
        >
          <BottomNavigationAction
            label="Sobre mi..."
            icon={<AssignmentIndIcon />}
            style={{
              color: value === 0 ? "#0d6efd" : "inherit",
              fontWeight: value === 0 ? "bold" : "normal",
            }}
          />
          <BottomNavigationAction label="Habilidades" icon={<SchoolIcon />} />
          <BottomNavigationAction label="Experiencia" icon={<QrCode2Icon />} />
          <BottomNavigationAction label="Portafolio" icon={<BookmarksIcon />} />
          <BottomNavigationAction
            label="Contacto"
            icon={<AlternateEmailIcon />}
          />
        </BottomNavigation>
      </Box>
    </div>
  );
};

export default PiePagina;
