import logo from "../../assets/img/qartos.png";

const Index = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-2">
          {" "}
          <img
            src={logo}
            className="img-fluid"
            alt="logo"
            style={{ width: 200, height: 90 }}
          />
        </div>
        <div className="col-sm-10">
          <h3>
            <strong>qartos</strong> (Red de Anfitriones)
          </h3>
          <p>
            qartos es una plataforma tecnológica que sirve de intermediación
            entre clientes que buscan habitaciones o inmuebles para arrendar con
            arrendadores que pueden prestar el servicio.
          </p>
          <div />
        </div>
        <div className="row">
          <h3 className="text-center text-muted">
            <strong>Características</strong>
          </h3>
          <br />

          <h5>
            <strong>Amplia gama de oferta:</strong>
          </h5>
          <p>
            Los proveedores de servicios tienen una amplia gama de inmuebles,
            desde apartamentos, casas, fincas, etc.
            <br />
            <strong>Tiempos abiertos:</strong> &nbsp; La necesidad del cliente
            puede ser desde un único días hasta meses o años, por ello no hay
            límite de tiempo de arrendamiento.
            <br />
            <strong>Calificaciones:</strong> &nbsp; tanto el cliente como el
            proveedor de servicios deben realizar una calificación. El cliente
            calificará al prestador y vicerversa. De esta se lleva un record en
            la calidad de prestación y como cliente.
          </p>
        </div>
      </div>
    </div>
  );
};
export default Index;
